<template>
    <div class="artical">
        <div class="mask" v-if="!showTitle"></div>
        <div :class="{'large-margin':!showTitle}">
            <h1 v-if="showTitle">{{title}}</h1>
            <div style="display:flex;">
                <div :class="{'noHide':showTitle}">
                    <el-input v-model="content" type="textarea" rows="15" :class="{'hide':!showTitle}"/>
                    <el-row style="margin:20px 0;">
                        <el-button type="info" icon="el-icon-s-opportunity" circle @click="hide"></el-button>
                        <el-button type="primary" @click="save" v-if="mode=='add'&&showTitle">新增</el-button>
                        <el-button type="primary" @click="edit" v-if="mode=='edit'&&showTitle">编辑</el-button>
                    </el-row>
                </div>
                <div style="flex:1;" v-if="!showTitle">
                    <img src="http://qa3wvi1lp.bkt.clouddn.com/%E6%88%AA%E5%B1%8F2020-05-12%20%E4%B8%8B%E5%8D%8811.25.39.png
" alt="" style="width:100%;">
                    <img style="width:100%"
                         src="http://qa3wvi1lp.bkt.clouddn.com/%E6%88%AA%E5%B1%8F2020-05-12%20%E4%B8%8B%E5%8D%8811.31.44.png"
                         alt="">
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'Artical',
        data() {
            return {
                mode: 'add',
                title: '',
                content: '',
                showTitle: false

            }
        },
        methods: {
            hide() {
                this.showTitle = !this.showTitle
            },
            save() {
                if(!this.content){
                   this.$message.warning('内容不能为空');
                   return false;
                }
                this.$post('artical/addArtical', {title: this.title, artical: this.content})
                    .then(res => {
                        console.log(res)
                        if (res.code == 200) {
                            this.$message.success('新增成功');
                            this.$router.push('/home')
                        }
                    })
                    .catch(err => {
                        console.log(err)
                    })
                console.log(this.content)
            },
            edit() {
                if(!this.content){
                    this.$message.warning('内容不能为空');
                    return false;
                }
                this.$post('artical/editArtical', {title: this.title, artical: this.content})
                    .then(res => {
                        if (res.code == 200) {
                            this.$message.success('编辑成功');
                            this.$router.push('/home')
                        }
                    })
                    .catch(err => {
                        console.log(err)
                    })
                console.log(this.content)
            },
            searchArtical(title) {
                this.$post('artical/getArtical', {title})
                    .then(res => {
                        if (res.code == 200) { //已有数据，现在属于编辑
                            this.mode = 'edit'
                            this.content = res.data[0].content;
                        } else if (res.code == 201) {//没有数据，现在属于新增
                            this.mode = 'add'
                        }
                    })
            }
        },
        mounted() {
            this.title = this.$route.query.title;
            this.showTitle=!(this.$store.state.ifHide);
            this.searchArtical(this.title)

        }
    }
</script>
<style lang="less" scoped>
    .artical {
        text-align: left;
        .large-margin {
            margin-top: 430px;
        }
        .mask {
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
            height: 430px;
            background: url('http://qa3wvi1lp.bkt.clouddn.com/%E6%88%AA%E5%B1%8F2020-05-12%20%E4%B8%8B%E5%8D%8811.35.40.png') no-repeat;
            background-size: cover;
            z-index: 99;
        }
        .noHide{
            width:100%;
        }
        .hide {
            width: 300px;
            height:300px;
        }
    }

</style>
